import React, {Component} from 'react';
import  {Switch, Route} from 'react-router-dom';

import Landing from './landing';
import Alcoholic from './alcoholic';
import NonAlcoholic from './nonalcoholic';
import OrdinaryDrinks from './ordinarydrinks';
import Cocktails from './cocktails';
import Recipe from './recipe';


class Routing extends Component{
    render(){
        return(
            <Switch>
                <Route exact path={process.env.PUBLIC_URL + '/'} component={Landing}/>
                <Route path="/alcoholic" component={Alcoholic}/>
                <Route path="/non-alcoholic" component={NonAlcoholic}/>
                <Route path="/ordinary-drinks" component={OrdinaryDrinks}/>
                <Route path="/cocktails" component={Cocktails}/>
                <Route path="/recipe/:itemId" component={Recipe}/>
            </Switch>
        )
    }
}

export default Routing;