import React, {Component} from 'react';



class Recipe extends Component{
    constructor(){
        super();
        this.state={
            item:[],
            category:[],
            ingredients:[],
            measurements:[],
            cocktails:[],
           // ordinary:[], 
            alcohol:[],
            nonAlcohol:[]
        }
    }

    async componentDidMount(){

        const itemID = this.props.match.params.itemId;
        const response = await fetch(`https://www.thecocktaildb.com/api/json/v1/1/lookup.php?i=`+itemID);
        const json = await response.json();
        const drink = json.drinks[0];
        this.setState({
            item : drink
        });

        const obj = JSON.stringify(drink);
        const arr = JSON.parse(obj);
        const key = Object.keys(arr);
        const value = Object.values(arr);
       /* this.setState({
            ingredients : key
        }); */
       // console.log(value);

        
        for(let i=21;i<=35;i++){
          //  console.log(value[i]);
            if(value[i]===null || value[i]===""){
             //   console.log("broke");
                break;
            }
            this.setState({ ingredients : this.state.ingredients.concat([key[i]])
            });
        }

        for(let i=36;i<=50;i++){
            if(value[i]===null || value[i]===""){
              //  console.log("broke");
                break;
            }
            this.setState({ measurements : this.state.measurements.concat([key[i]])
            });
        }

        const AnoResponse = await fetch(`https://www.thecocktaildb.com/api/json/v1/1/filter.php?c=`+this.state.item.strCategory);
        const json2 = await AnoResponse.json();
        const alco = json2.drinks.length-1;
        for(let i = 0; i<6; i++){
            const rand =  Math.floor((Math.random() * alco) + 1);
            this.setState({ 
                category: this.state.category.concat([json2.drinks[rand]])
            });
        }

        const cocResponse = await fetch(`https://www.thecocktaildb.com/api/json/v1/1/filter.php?c=Cocktail`);
        const coc = await cocResponse.json();
        const cocL = coc.drinks.length-1;
        for(let i = 0; i<3; i++){
            const rand =  Math.floor((Math.random() * cocL) + 1);
            this.setState({ 
                cocktails: this.state.cocktails.concat([coc.drinks[rand]])
            });
        }

        const alcResponse = await fetch(`https://www.thecocktaildb.com/api/json/v1/1/filter.php?a=Alcoholic`);
        const alc = await alcResponse.json();
        const alcL = alc.drinks.length-1;
        for(let i = 0; i<3; i++){
            const rand =  Math.floor((Math.random() * alcL) + 1);
            this.setState({ 
                alcohol: this.state.alcohol.concat([alc.drinks[rand]])
            });
        }

        const nonResponse = await fetch(`https://www.thecocktaildb.com/api/json/v1/1/filter.php?a=Non_Alcoholic`);
        const non = await nonResponse.json();
        const nonL = non.drinks.length-1;
        for(let i = 0; i<3; i++){
            const rand =  Math.floor((Math.random() * nonL) + 1);
            this.setState({ 
                nonAlcohol: this.state.nonAlcohol.concat([non.drinks[rand]])
            });
        }

      {/*  const ordResponse = await fetch(`https://www.thecocktaildb.com/api/json/v1/1/filter.php?c=Ordinary_Drink`);
        const ord = await ordResponse.json();
        const ordL = ord.drinks.length-1;
        for(let i = 0; i<4; i++){
            const rand =  Math.floor((Math.random() * ordL) + 1);
            this.setState({ 
                ordinary: this.state.ordinary.concat([ord.drinks[rand]])
            });
        }

    */}
        
    }

    showItem(e){
        console.log(e);
        this.props.history.push("/recipe/"+e);
        window.location.reload();
    }
    
    render(){
        return(
            <div className="row" style={{paddingBottom:"3%",paddingTop:"7%", margin:"auto", backgroundColor:"grey", width:"100%"}}>

                <h1 className="col-md-12" style={{textAlign:"center"}}>{this.state.item.strDrink}</h1>


                    <div className="col-md-7" style={{backgroundColor:"white", padding:"0%", width:"100%", margin:"auto"}}>
                        <img src={this.state.item.strDrinkThumb} style={{width:"100%", height:"auto"}} alt="thumbnail"/>

                        <div className="row" style={{ padding:"5%", width:"100%", margin:"auto" }}>
                            <h6 style={{ margin:"auto"}}>{this.state.item.strAlcoholic}</h6>
                            <h6  style={{margin:"auto", textAlign:"right"}}>{this.state.item.strGlass}</h6>
                        </div>

                        <div className="row" style={{width:"90%", backgroundColor:"black", color:"white", margin:"auto"}}>
                            <div className="breaker col-md-12"><h3 style={{fontSize:"20px"}}> Some {this.state.item.strCategory}s</h3></div>
                            
                            <div className="row gridItems" id="category">
                                {this.state.category.map((catelog)=>(
                                    <div 
                                        key={catelog.idDrink}
                                        id={catelog.idDrink} 
                                        className="col-md-4" 
                                        onClick={() => this.showItem(catelog.idDrink)}
                                    >
                                        <img src={catelog.strDrinkThumb} style={{width:"100%"}} alt="thumbnail"/>
                                        <h6 style={{fontSize:"20px"}}>{catelog.strDrink}</h6>
                                    </div>
                                ))}
                            </div>
                        </div>

                        
                        <div className="row" style={{width:"100%", margin:"auto"}}>
                            <h4 className="col-md-12" style={{textAlign:"center"}}> <b>{this.state.item.strDrink}</b></h4>
                            <p className="col-md-12" style={{textAlign:"center"}}>
                                <b>{this.state.item.strCategory} | {this.state.item.strGlass} | {this.state.item.strAlcoholic}</b>
                            </p>
                            
                            <div className=" row col-md-7 text-center" >


                                <h4 className="col-md-12" style={{textAlign:"center"}}>Ingredients used in <br/><b>{this.state.item.strDrink}</b></h4>
                                <div className="col-md-12 row" style={{width:"100%"}}>
                                    <div className="col-5 text-center" >
                                        {this.state.measurements.map((measurement)=>(
                                            <p>{this.state.item[measurement]}</p>
                                            
                                        ))}
                                    </div>
                                    <div className="col-7 text-center" style={{width:"100%"}}>
                                        {this.state.ingredients.map((ingredient)=>(
                                            <p>{this.state.item[ingredient]}</p>
                                            
                                        ))}
                                    </div>
                                </div>
                            </div>

                            <div className="col-md-5">
                                <h4 className="col-md-12" style={{textAlign:"center"}}>How to make <br/><b>{this.state.item.strDrink}</b></h4>
                                <p className="col-md-12">{this.state.item.strInstructions}</p>
                            </div>
                        </div>
                    </div>


{/*//////////////////////////////////////////////////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////////////////////////////////////////////////////////
//////////////////////////////////////////////////////////////////////////////////////////////////////////////////*/}
                    <div className="col-md-4 "  style={{width:"100%", padding:"0%"}}>

                        <div className="col-md-12 row" style={{backgroundColor:"white", marginTop:"2%"}}>
                            <div className="breaker col-md-12"><h3 style={{fontSize:"20px", width:"98%"}}>Some Cocktails</h3></div>
                            
                            {this.state.cocktails.map((cocktail)=>(
                                <div className="gridItems"
                                style={{width:"100%", margin:"none", textAlign:"left"}}
                                 onClick={() => this.showItem(cocktail.idDrink)}>
                                    <div>
                                        <img src={cocktail.strDrinkThumb} alt="thumb"  style={{width:"40%", height:"auto", padding:"5%"}}/>
                                        <span className="text-center">{cocktail.strDrink}</span>
                                    </div>
                                </div>
                            ))}
                        </div>
                        



                   {/*      <div className="col-md-12 row" style={{backgroundColor:"white", marginTop:"2%"}}>
                            <div className="breaker col-md-12"><h3 style={{fontSize:"20px", width:"98%"}}>Some Ordinary Drinks</h3></div>
                            
                            {this.state.ordinary.map((ordinaries)=>(
                                <div className="gridItems"
                                style={{width:"100%", margin:"none", textAlign:"left"}}
                                 onClick={() => this.showItem(ordinaries.idDrink)}>
                                    <div>
                                        <img src={ordinaries.strDrinkThumb} alt="thumb"  style={{width:"40%", height:"auto", padding:"5%"}}/>
                                        <span>{ordinaries.strDrink}</span>
                                    </div>
                                </div>
                            ))}
                        </div> 
                        
                    */}





                        <div className="col-md-12 row" style={{backgroundColor:"white", marginTop:"2%"}}>
                            <div className="breaker col-md-12"><h3 style={{fontSize:"20px", width:"98%"}}>Some Alcoholic Drinks</h3></div>
                        
                            {this.state.alcohol.map((alcohols)=>(
                                <div className="gridItems"
                                style={{width:"100%", margin:"none", textAlign:"left"}}
                                 onClick={() => this.showItem(alcohols.idDrink)}>
                                    <div>
                                        <img src={alcohols.strDrinkThumb} alt="thumb"  style={{width:"40%", height:"auto", padding:"5%"}}/>
                                        <span className="text-center">{alcohols.strDrink}</span>
                                    </div>
                                </div>
                            ))}
                        </div>



                        <div className="col-md-12 row" style={{backgroundColor:"white", marginTop:"2%"}}>
                            <div className="breaker col-md-12"><h3 style={{fontSize:"20px", width:"98%"}}>Some Non-Alcoholic Drinks</h3></div>
                       
                            {this.state.nonAlcohol.map((nonAlcohols)=>(
                                <div className="gridItems"
                                style={{width:"100%", margin:"none", textAlign:"left"}}
                                 onClick={() => this.showItem(nonAlcohols.idDrink)}>
                                    <div>
                                        <img src={nonAlcohols.strDrinkThumb} alt="thumb"  style={{width:"40%", height:"auto", padding:"5%"}}/>
                                        <span className="text-center">{nonAlcohols.strDrink}</span>
                                    </div>
                                </div>
                            ))}
                        </div>


                    </div>
                </div>
        );
    }
}

export default Recipe;