import React, {Component} from 'react';



class OrdinaryDrinks extends Component{
    constructor(){
        super();
        this.state={
            alcoholics:[]
        }
    }

    async componentDidMount(){

        const alcResponse = await fetch(`https://www.thecocktaildb.com/api/json/v1/1/filter.php?c=Ordinary_Drink`);
        const alc = await alcResponse.json();
        const alcL = alc.drinks.length-1;
        for(let i = 0; i<10; i++){
            const rand =  Math.floor((Math.random() * alcL) + 1);
            this.setState({ 
                alcoholics: this.state.alcoholics.concat([alc.drinks[rand]])
            });
        }
    }

    showItem(e){
        console.log(e);
        this.props.history.push("/recipe/"+e);
        window.location.reload();
    }

    render(){
        return(
            <div className="row" style={{paddingBottom:"3%",paddingTop:"7%", paddingLeft:"3%", paddingRight:"3%", margin:"auto", backgroundColor:"grey", width:"100%"}} >
                <h1 className="col-md-12 text-center">Ordinary Drinks</h1>
                {this.state.alcoholics.map((alcohol)=>(
                    <div className="col-md-5 col-md-offset-1 row nameImg bg-light"
                     style= {{width:"80%", margin:"auto", textAlign:"left" ,marginTop:"3%"}}
                    onClick={() => this.showItem(alcohol.idDrink)}>
                        <img className="col-md-6" src={alcohol.strDrinkThumb} style={{width:"50%", height:"auto", paddingTop:"2%", paddingBottom:"2%"}} alt="thumbnail"/>
                        <b className="col-md-6 text-center" style={{margin:"auto", paddingLeft:"5%", fontSize:"20px"}}>{alcohol.strDrink}</b>
                    </div>
                ))}
                

            </div>
        );
    }
}

export default OrdinaryDrinks;