import React, {Component} from 'react';
//import {Redirect} from 'react-router-dom';



class Landing extends Component{
    constructor() {
        super();
        this.state = {
            cocktails: [],
            alcoholics: [],
            nonAlcoholics: []
        };
      }

      async componentDidMount() {
        //////////////////////////////////////////////
        for(let i=0;i<6;i++){
            const response = await fetch(`https://www.thecocktaildb.com/api/json/v1/1/random.php`);
            const json = await response.json();
            this.setState({ 
                cocktails: this.state.cocktails.concat([json.drinks[0]])
            });
        }

        //////////////////////////////////////////////////////////
        const AnoResponse = await fetch(`https://www.thecocktaildb.com/api/json/v1/1/filter.php?a=Alcoholic`);
        const json2 = await AnoResponse.json();
        const alco = json2.drinks.length;
        for(let i = 0; i<6; i++){
            const rand =  Math.floor((Math.random() * alco) + 1);
            this.setState({ 
                alcoholics: this.state.alcoholics.concat([json2.drinks[rand]])
            });
        }
        

        //////////////////////////////////////////////////////////////
        const nonResponse = await fetch(`https://www.thecocktaildb.com/api/json/v1/1/filter.php?c=Ordinary_Drink`);
        const json3 = await nonResponse.json();
        const nonAlco = json3.drinks.length;
        for(let i = 0; i<6; i++){
            const rand =  Math.floor((Math.random() * nonAlco) + 1);
            this.setState({ 
                nonAlcoholics: this.state.nonAlcoholics.concat([json3.drinks[rand]])
            });
        }
        console.log(this.state.nonAlcoholics);
        
    }

    //////////////////////////////////////// routing to recipes /////////////////////////////////////////////
    showItem(e){
        this.props.history.push("/recipe/"+e);
    }

    /////////////////////////////////////// resndering landing page ///////////////////////////////////////// 
    render(){
        return(
            <div style={{paddingTop:"7%"}}>
                {/* top image */}
                <div id="banner" style={{width:"50%", margin:"auto",paddingBottom:"1%"}}>
                    <img src={require("../img/banner.jpg")} alt="banner" style={{width:"100%", height:"auto"}}/>
                </div>

                {/* middle big immage and video container */}
                <div id="imgVid">
                    {/* heading with background */}
                    <h1>Cocktail Recipes</h1>

                    {/* video frame */}
                    <iframe title="title" src="https://www.youtube.com/embed/tgbNymZ7vqY"></iframe> 
                </div>

                <div className="breaker"><h3>COCKTAILS</h3></div> {/* line breaker with text in middle */}

                {/* images for cocktail category */}
                <div className="row gridItems" id="cocktails">
                    {this.state.cocktails.map((cocktail)=>(
                        <div 
                            key={cocktail.idDrink}
                            id={cocktail.idDrink} 
                            className="col-lg-4 col-sm-6" 
                            onClick={() => this.showItem(cocktail.idDrink)}
                        >
                            <img src={cocktail.strDrinkThumb} alt="thumbnail"/>
                            <h5>{cocktail.strDrink}</h5>
                        </div>
                    ))}
                </div>

                <div className="breaker"><h3>ALCOHOLIC DRINKS</h3></div>

                {/* images for alcholic category */}
                <div className="row gridItems" id="alcoholics">
                    {this.state.alcoholics.map((alcohol)=>(
                        <div 
                            key={alcohol.idDrink}
                            id={alcohol.idDrink} 
                            className="col-lg-4 col-sm-6" 
                            onClick={() => this.showItem(alcohol.idDrink)}
                        >
                            <img src={alcohol.strDrinkThumb} alt="thumbnail"/>
                            <h5>{alcohol.strDrink}</h5>
                        </div>
                    ))}
                </div>

                <div className="breaker"><h3>ORDINARY DRINKS</h3></div>

                {/* images for non-alcholic category */}
                <div className="row gridItems" id="nonAlcoholics">
                    {this.state.nonAlcoholics.map((nonAlcohol)=>(
                        <div 
                            key={nonAlcohol.idDrink}
                            id={nonAlcohol.idDrink} 
                            className="col-lg-4 col-sm-6" 
                            onClick={() => this.showItem(nonAlcohol.idDrink)}
                        >
                            <img src={nonAlcohol.strDrinkThumb} alt="thumbnail"/>
                            <h5>{nonAlcohol.strDrink}</h5>
                        </div>
                    ))}
                </div>

            </div>
        );
    }
}

export default Landing;